    text-decoration: none; }

b, strong, h2, h3, h4 {
  font-weight: 600; }

.app-title {
  font-size: 2.4rem;
  font-weight: 300;
  padding-bottom: 5px;
  border-bottom: 1px solid #d4d9e3; }

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-100px); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

.fadeInLeft {
  animation-name: fadeInLeft;
  animation-duration: 1s;
  animation-fill-mode: both; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.fade-in {
  animation-delay: 0;
  animation-name: fadeIn;
  animation-duration: 1s;
  animation-fill-mode: forwards; }

/**
 * Dropdown animation
 */
.app-alerts__list {
  backface-visibility: hidden;
  transform: scale(0);
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.77, 0, 0.175, 1);
  visibility: hidden; }

.is-alerts-visible .app-alerts__list {
  transform: scale(1);
  opacity: 1;
  visibility: visible; }

