body {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.3;
  min-width: 320px;
  background-color: #F8F8F8;
  color: #183152; }

.accessibility-links {
  position: fixed;
  z-index: 99;
  top: 10px;
  left: 10px; }
  .accessibility-links > a {
    display: inline-block;
    padding: 3px 5px; }
  .accessibility-links .sr-only:active, .accessibility-links .f-icon__text:active,
  .accessibility-links .sr-only:focus,
  .accessibility-links .f-icon__text:focus {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto; }

.page-wrap {
  position: relative;
  padding-left: 55px;
  transition: all 0.3s cubic-bezier(0.77, 0, 0.175, 1); }
  .has-nav-expanded .page-wrap {
    padding-left: 240px; }

.wrapper, .app-content {
  position: relative;
  margin: 0;
  width: 100%;
  padding: 1rem 2rem; }

.app-content {
  min-width: 265px;
  min-height: calc(100vh - 55px); }

.app-view {
  padding: 2rem 0; }

.app-alerts {
  position: fixed;
  right: 0;
  top: 0;
  z-index: 9;
  width: 50px;
  height: 46px;
  background-color: #fff;
  box-shadow: -2px 0 4px rgba(0, 0, 0, 0.2);
  transition: all 0.3s cubic-bezier(0.77, 0, 0.175, 1); }
  .app-alerts.is-alerts-visible {
    width: 240px;
    height: 100%; }

.app-alerts__title {
  background-color: #689f38;
  padding: 12px 15px 13px 25px;
  text-transform: uppercase;
  color: #fff;
  font-size: 2rem;
  font-weight: 300;
  position: relative;
  line-height: 1;
  cursor: pointer;
  min-width: 240px; }
  .app-alerts__title::before {
    content: "";
    border: 6px solid transparent;
    border-right-color: inherit;
    vertical-align: middle;
    display: inline-block;
    opacity: 0.5;
    position: absolute;
    left: 5px;
    top: 15px;
    transition: all 0.3s cubic-bezier(0.77, 0, 0.175, 1); }
    .is-alerts-visible .app-alerts__title::before {
      transform: rotate(180deg); }
  .app-alerts__title__icon {
    vertical-align: middle; }
  .app-alerts__title__text {
    vertical-align: middle;
    padding-left: 10px;
    display: inline-block;
    opacity: 0;
    transition: all 0.3s cubic-bezier(0.77, 0, 0.175, 1); }
    .is-alerts-visible .app-alerts__title__text {
      opacity: 1; }

.app-alerts__list {
  padding: 0 5px 0 20px;
  list-style: none;
  margin: 0; }
  .app-alerts__list__item {
    padding: 20px 10px 12px 0;
    border-bottom: 1px solid #d4d9e3;
    line-height: 1.1;
    color: #375D81; }
    .app-alerts__list__item:last-child {
      border-bottom: 0; }
    .app-alerts__list__item__number {
      font-size: 3.4rem;
      float: left;
      margin-right: 5px;
      line-height: 0.9; }
    .app-alerts__list__item__text {
      font-size: 1.5rem; }

html {
  font-size: 10px;
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0; }

input[placeholder] {
  text-overflow: ellipsis; }
  input[placeholder] ::placeholder {
    font-weight: 300;
    text-overflow: ellipsis; }

[hidden] {
  display: none !important; }

img {
  max-width: 100%;
