.sr-only, .f-icon__text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.cfx:before, .cfx:after {
  content: " ";
  display: table; }

.cfx:after {
  clear: both; }
